<div class="chats" v-bind:class="{ full : isFull, mini: isMini }"><div class="ui inverted big menu" id="menu"><div class="item ui form" v-show="isFull"><div class="ui input"><input v-model="key" placeholder="搜尋"></div><!-- .ui.buttons(v-if="!user")button.ui.orange.button(@click="loginGoogle()")
  i.google.icon
  | 登入--></div><div class="right menu"><a class="item" v-if="!isFull" @click="isFull = true; isMini = false; reCount()"><i class="comments icon"></i>群聊<div class="red note" v-show="fil(chats).length &gt; read">{{ fil(chats).length - read }}</div></a><a class="item" v-if="!isMini" @click="isFull = false; isMini = true"><i class="compress icon"></i>縮小</a></div></div><div id="box"><div class="ui list"><div class="item" v-for="(c, idx) in fil(chats).slice(fil(chats).length - 5, fil(chats).length)" v-bind:key="c.t"><p v-show="edit !== c"> <router-link :to="'/flag/' + c.uid"><img class="ui avatar" :src="c.photoURL || 'http://graph.facebook.com/' + c.uid + '/picture'" alt="^_^"></router-link><a @click="key = c.l" v-bind:class="c.l">[{{c.l}}]</a><!-- a(@click="edit = c" v-show="c.uid == uid")i.edit.icon(title="edit")--><span class="text">{{ c.n }} : {{ c.t }}</span><span class="gray" v-show="isFull">&nbsp;&nbsp;-{{ countDateDiff(c.time) }}</span></p><!--.ui.form(v-show="edit == c").ui.input
  input.input(v-model="c.t", placeholder="更新")
  a.ui.green.small.button(@click.stop="edit = ''; updateChat(c)") 更新
--></div><div class="item preview" v-if="p.t"><router-link :to="'/flag/' + p.uid"><img class="ui avatar" :src="p.photoURL || '/static/img/handshake0.png'"></router-link><a @click="key = p.l" v-bind:class="p.l">[{{p.l}}] (預覽)</a>{{ p.n }} : {{ p.t }}<span class="gray" v-show="isFull">&nbsp;&nbsp;-{{ countDateDiff(p.time) }}</span></div><div class="item" v-if="uid"><div class="ui form"><div class="field"><img class="ui avatar" :src="photoURL"><input class="input" v-model="msg" placeholder="在想什麼嗎?" autofocus></div><div class="inline fields"><div class="field" v-for="l in labels"><div class="ui radio checkbox"><input type="radio" name="l" v-model="label" :value="l"><label><a @click="label=l" v-bind:class="l">{{ l }}</a></label></div></div><div class="ui button group"><a class="ui blue small button" @click="preview">預覽</a><a class="ui green small button" @click="addChat">留言</a></div></div></div></div><div class="item" v-else><div class="ui big buttons" v-if="!uid"><button class="ui orange button" @click="loginGoogle()"><i class="google icon"></i>登入以留言 </button></div></div></div></div></div>