te), mySearch) + '...'"></span><br></p><p class="descrtpion" v-else><span class="text" v-html="highlight(h.note, mySearch)"></span><br></p></div></div><div class="filler"></div><div class="ui bottom attached stackable buttons" :class="{vertical: !full || thin}"><div class="ui green button" @click="addBook(h.uid || h.idx)" v-if="uid &amp;&amp; (!book || book.indexOf(h.uid || h.idx) == -1)"><i class="book icon"></i>登入名簿</div><div class="ui red button" @click="removeBook(book.indexOf(h.uid || h.idx))" v-else-if="uid &amp;&amp; book &amp;&amp; book.indexOf(h.uid || h.idx) &gt; -1"><i class="book icon"></i>從名簿移除</div><!-- router-link.ui.blue.basic.button(to="/profile" v-else)i.user.icon
